import React from "react";
// import { DateTime,dateTime } from '@grafana/data';
// import {IconButton, Slider, stylesFactory,DateTimePicker as DateSelect} from '@grafana/ui';
import { IconButton, stylesFactory,Input,Slider} from '@grafana/ui';
// import  DateTimePicker  from '@grafana/ui';
import {css} from "emotion"
import * as d3 from "d3";
import { Timer } from 'd3';
interface Props {
    onChange: (value: number) => void,
    timeStamp: Date[],
    value: number,
}
interface States {
    currentIndex:number
    isPlay: boolean
}
const format = d3.timeFormat('%Y-%m-%dT%H:%M')
export class TimeSelection extends React.Component<Props, States>{
    timer:Timer|null = null;
    constructor(props:Props) {
        super(props);
        this.state = {
            currentIndex:props.value,
            isPlay:false,
        }
    }
    componentWillUnmount(): void {
        this.onStop()
    }
    componentDidUpdate(prevProps: Readonly<Props>, prevState: Readonly<States>, snapshot?: any): void {
        if((this.props.value!==prevProps.value)&&(this.props.value!==this.state.currentIndex)){
            this.setState({currentIndex:this.props.value});
            this.onStop();
        }
    }

    onPlay(){
        if(!this.state.isPlay){
            this.setState({isPlay:true});
            if(this.timer)
                this.timer.stop();
            this.timer = d3.interval(()=>{
                let currentIndex =  this.props.value+1;
                if (this.props.value+1 >= this.props.timeStamp.length-1){
                    currentIndex = this.props.timeStamp.length-1;
                    this.onStop();
                }
                this.setState({currentIndex});
                this.props.onChange(currentIndex)
            },1000);
        }else{
            this.onStop();
        }
    }
    onStop(){
        if(this.timer)
            this.timer.stop();
        this.setState({isPlay:false});
    }
    render(){
        const {value,timeStamp} = this.props;
        const {isPlay} = this.state;
        const styles = getStyles();
        return <div className={styles.root}>
            <IconButton name={isPlay?"pause":"play"} size={'xl'} onClick={this.onPlay.bind(this)}/>
            <div className={styles.progressBarWrapper}>
                <Input name="timePoint" css value={format(new Date(timeStamp[value]))} type={'datetime-local'} onChange={(event)=>{
                    // @ts-ignore
                    this.props.onChange(d3.bisect(timeStamp,+new Date(event.currentTarget.value)));
                }}/>
            </div>
            <Slider value={value} key={isPlay?value:0} min={0} step={1} max={timeStamp.length-1} tooltipAlwaysVisible={false} onChange={event=>this.props.onChange(event??0)}/>
        </div>
    }
}

const getStyles = stylesFactory(() => {
    return {
        progressBarWrapper: css`
      position: relative;
    `,
        root: css`
      display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
    flex-wrap: wrap;
    width:270px;
    & .rc-slider + div 
      {display: none}
    `,
    }
})
