import React from 'react';
import {DataFrame, Field, PanelProps, Vector} from '@grafana/data';
import {SimpleOptions} from 'types';
import {css} from 'emotion';
import { stylesFactory} from '@grafana/ui';
// import{Timeline} from './Timeline'
import * as d3 from 'd3';
import {ScaleLinear} from "d3-scale";
import * as _ from "lodash"
import { TimeSelection } from 'TimeSelection';
// import { Slider } from 'TimeSLider';
// import {Drawer} from "./drawer";

interface DataObject extends Record<string,Date|number|string|undefined|boolean|null|any[]> {
    order: number,
    highlight?:boolean,
    stackdelta?: any[]
};
type DataByTime = Record<string,Record<string,DataObject>>;

interface Props extends PanelProps<SimpleOptions> {
    // margin?: {top:number,bottom:number,left:number,right:number},
    // scalezoom?: number
}
interface RankCurrent extends Array<string>{
    object: Record<string, number>,
    deltarank?: any[]
}
type RankCompute = Record<string, Record<string, number[]>>
type Rank = Record<string, Record<string, number>[]>
interface States {
    datas: Record<string,any>[],
    dataByTime: DataByTime,
    data:DataObject[],
    dimensions: Dimension[],
    selectedService: keyof DataObject,
    selectedIndex: number,
    selectedTime: Date,
    timeStamp: Date[],
    freeze?:boolean,
    selectedData?:DataObject,
    scalezoom?:1,
    margin?: {top:number,bottom:number,left:number,right:number},
    ranking: {
        byComputer:RankCompute,
        byMetric:Rank,
        byUser:Rank
    },
    order: RankCurrent
}

interface Dimension {
    range: [number,number] | [undefined, undefined],
    scale: ScaleLinear<any, any, any>,
    order: number,
    key: string,
    active: number[],
    tickFormat?: Function
}
function createOrder():RankCurrent{
    // @ts-ignore
    let a: RankCurrent = new Array<string>();
    a.object = {};
    return a;
}
export class SimplePanel extends React.Component<Props, States>{
    svgRef = React.createRef<SVGSVGElement>();
    widthView(){return this.props.width*(this.state.scalezoom??1)}
    heightView(){return this.props.height*(this.state.scalezoom??1)}
    widthG(){return this.widthView()-(this.state.margin??{top:0,bottom:0,left:0,right:0}).left-(this.state.margin??{top:0,bottom:0,left:0,right:0}).right}
    heightG(){return this.heightView()-(this.state.margin??{top:0,bottom:0,left:0,right:0}).top-(this.state.margin??{top:0,bottom:0,left:0,right:0}).bottom}
    centerX(){return (this.state.margin??{top:0,bottom:0,left:0,right:0}).left+this.widthG()/2;}
    centerY(){return (this.state.margin??{top:0,bottom:0,left:0,right:0}).top+this.heightG()/2;}
    r(){return (d3.min([this.props.width, this.props.height])??0) / 2-20}
    trajectory={bandwidth:10,thresholds:10,colorScheme:"interpolateViridis"}
    public static defaultProps = {
        width:100,
        height:100,
        margin :{top:0,bottom:0,left:0,right:0},
        scalezoom: 1,
        // nodePerRing: 50,
        // nodeSize:5
    }
    state:States = {
            data: [],
            datas: [],
            dataByTime: {},
            dimensions: [],
            selectedService: '',
            selectedIndex: 0,
            selectedTime: new Date(),
            timeStamp:[],
            ranking: {
                byComputer:{},
                byMetric:{},
                byUser:{}
            },
            order:createOrder()
        }
    constructor(props:Props) {
        super(props);
    }
    componentDidMount(){
        const opt:Partial<States> = this.handleData();
        if (opt.dimensions)
            opt.selectedService = opt.dimensions[0].key;
        if (opt.timeStamp){
            opt.selectedIndex = 0;
            opt.selectedTime = opt.timeStamp[opt.selectedIndex]??new Date();
        }
        opt.data = this.extractData(opt.dataByTime??{},opt.selectedTime??this.state.selectedTime);
        this.sortByService(opt.data,''+opt.selectedService)
        this.setState(opt as NonNullable<States>);
    }
    onChangeTime(selectedIndex:number){
        const opt:Partial<States> = {}
        opt.selectedIndex = selectedIndex;
        opt.selectedTime = this.state.timeStamp[selectedIndex]??new Date();
        opt.data = this.extractData(this.state.dataByTime,opt.selectedTime??this.state.selectedTime);
        this.sortByService(opt.data,this.state.selectedService);
        if (this.state.selectedData)
            {
                // @ts-ignore
                opt.selectedData = opt.data.find(d=> this.state.selectedData.id === d.id);
            }
        this.setState(opt as NonNullable<States>);
    }
    componentDidUpdate(prevProps: Readonly<Props>, prevState: Readonly<States>, snapshot?: any): void {
        if (this.props.data!==prevProps.data){
            const opt:Partial<States> = this.handleData();
            if (opt.dimensions)
                opt.selectedService = opt.dimensions[0].key;
            if (opt.timeStamp){
                opt.selectedIndex = 0;
                opt.selectedTime = opt.timeStamp[opt.selectedIndex]??new Date();
            }
            opt.data = this.extractData(opt.dataByTime??{},opt.selectedTime??this.state.selectedTime);
            this.sortByService(opt.data,''+opt.selectedService)
            this.setState(opt as NonNullable<States>);
        }
    }
    sortByService(data: DataObject[],service: keyof DataObject){
        data.sort((a:DataObject,b:DataObject)=>((a[service] as number) - (b[service] as number)))
            .forEach((d,i)=>d.order=i);
    }
    draw_trajectory_contours(d: DataObject,data:(number|undefined)[],radius:Function,spiralScale:Function,theta:Function){
        let dataDraw = data;
        // if (_.isArray(data[0]))
        //     dataDraw = _.flatten(data);
        dataDraw = dataDraw.filter((d:number|undefined)=>d!=undefined);
        const contours = d3.contourDensity<number|undefined>()
            .x(e=>radius(spiralScale(e))*Math.sin(theta(spiralScale(e)))+this.centerX())
            .y(e=>-radius(spiralScale(e))*Math.cos(theta(spiralScale(e)))+this.centerY())
            .size( [this.widthG(), this.heightG()])
            .bandwidth(this.trajectory.bandwidth)(dataDraw);

        const lastbase = _.last(d3.contourDensity().size( [this.widthG(), this.heightG()])
            .bandwidth(this.trajectory.bandwidth) (dataDraw.map((d:any)=>[this.widthG()/2, this.heightG()/2])));
        if(lastbase){
            const base = lastbase.value;

            const range = d3.extent(contours, d => d.value/base*100 );
            const color = d3.scaleSequential(d3.interpolateViridis)
                .domain([range[0]??0,range[1]??0]);
            return contours.map(d=><path className={"trajectory trajectoryPath"}
                                         transform={`translate(${-this.centerX()},${-this.centerY()})`}
                                         d={d3.geoPath()(d)??''}
                                         style={{fill:color(d.value/base*100)}}
            />)
        }else{
            return ''
        }
        // g.select('g.trajectoryHolder').selectAll('path.trajectory.trajectoryPath')
        //     .data(contours)
        //     .join('path')
        //     .attr('transform',`translate(${-graphicopt.centerX()},${-graphicopt.centerY()})`)
        //     .attr('class','trajectory trajectoryPath')
        //     .attr("d", d3.geoPath())
        //     .style('stroke',null)
        //     .style("fill", d => color(d.points)).style('opacity',null);
    }
    handleData(){
        const {data} = this.props;
        const dataByTime: DataByTime = {};
        const datas = new Array();
        let nodeLists = new Map();
        const dimensions:Dimension[] = [];
        let dimensionObject: Record<string,Dimension> = {};
        let timeObject: Record<string,Date> = {};
        let ranking = {byComputer:{} as RankCompute,byMetric:{} as Rank,byUser:{} as Rank};

        function getDim(range: [number,number], order: number, dim: string): Dimension {
            const scale = d3.scaleLinear().domain(range);

            const dimControl: Dimension = {
                range,
                scale,
                order,
                key: dim,
                active: new Array<number>(),
                tickFormat: undefined
            };
            return dimControl;
        }

        function handleDataWithname(serie: DataFrame) {
            const dim = serie.refId ?? '';
            const times = serie.fields.find(e => e.name.trim().toLowerCase() === 'time');
            const values = serie.fields.find(e => e.name === 'value');
            const nodeids = serie.fields.find(e => e.name === 'name');
            const dimTarget:( Dimension | undefined ) = dimensionObject[dim];
            let range: [number, number] = dimTarget?[dimTarget.range[0]??Infinity,dimTarget.range[1]??-Infinity]:[Infinity, -Infinity];
            if (nodeids && nodeids.values && times && times.values && values && values.values) {
                const nodeid = nodeids.values.toArray();
                nodeid.forEach((id, i) => {
                    if (!dataByTime.hasOwnProperty(id)) {
                        dataByTime[id] = {};
                    }
                    const val = values.values.get(i);
                    if (!dataByTime[id][times.values.get(i)]) {
                        const item = {time: times.values.get(i),order:0, id};
                        datas.push(item);
                        nodeLists.set(id, true);
                        dataByTime[id][times.values.get(i)] = item;
                    }
                    dataByTime[id][times.values.get(i)][dim] = val;
                    if (val < range[0])
                        range[0] = val;
                    if (val > range[1])
                        range[1] = val;
                })
            }
            if (!dimTarget) // new Dimension
            {
                dimensionObject[dim] = getDim(range, dimensions.length, dim);
                dimensions.push(dimensionObject[dim]);
            }else{
                dimTarget.range = range;
                dimTarget.scale.domain(range);
            }
        }

        function extraDimData(id_1: string, dim_1: string, d: Field<any, Vector<number|string>>, times: Field<any, Vector<number|string>>) {
            var _a, _b;
            if (!dataByTime[id_1]) {
                dataByTime[id_1] = {};
            }

            var dimTarget = dimensionObject[dim_1];
            var range_1 = (dimTarget ? [(_a = dimTarget.range[0]) !== null && _a !== void 0 ? _a : Infinity, (_b = dimTarget.range[1]) !== null && _b !== void 0 ? _b : -Infinity] : [Infinity, -Infinity]) as [number, number];
            d.values.toArray().forEach(function (val:any, i:number) {
                if (!dataByTime[id_1][times.values.get(i)]) {
                    var item = {
                        time: times.values.get(i),
                        order: 0,
                        id: id_1
                    };
                    datas.push(item);
                    nodeLists.set(id_1, true);
                    dataByTime[id_1][times.values.get(i)] = item;
                }

                dataByTime[id_1][times.values.get(i)][dim_1] = val;

                if (val !== null) {
                    if (val < range_1[0]) range_1[0] = val;
                    if (val > range_1[1]) range_1[1] = val;
                }
            });

            if (!dimTarget) // new Dimension
            {
                dimensionObject[dim_1] = getDim(range_1, dimensions.length, dim_1);
                dimensions.push(dimensionObject[dim_1]);
            } else {
                dimTarget.range = range_1;
                dimTarget.scale.domain(range_1);
            }
        }

        function handleDataWithnames(serie: DataFrame) {
            const times = serie.fields.find(e => e.name.trim().toLowerCase() === 'time');
            if (times) {
                serie.fields.filter(e => e.type === 'number').forEach(d => {
                    if (d.labels) {
                        var dim_1 = d.name;
                        var id_1 = '';

                        if (dim_1 === 'value') {
                            var str = Object.values(d.labels)[0].split('|').map(function (d) {
                                return d.trim();
                            });
                            id_1 = str[0].trim();
                            dim_1 = str[1].trim();
                        } else {
                            id_1 = Object.values(d.labels)[0].replace('|', '').trim();
                        }

                        extraDimData(id_1, dim_1, d, times);
                    }else if(d.name.split('|').length===2){
                        let str = d.name.split('|');
                        let id = str[1].trim();
                        let dim = str[0].trim();
                        extraDimData(id, dim, d, times);
                    }
                })
            }
        }
        data.series.forEach((serie) => {
            const time = serie.fields.find(e=>e.name.trim().toLowerCase() === 'time');
            if (time){
                time.values.toArray()
                    .forEach(t=>{
                        timeObject[t]=t;
                    });
            }
            if (serie.fields.find(e => e.name === 'name'))
                handleDataWithname(serie);
            else
                handleDataWithnames(serie)
        });
        // const timeRange: [number,number] |[undefined,undefined] = d3.extent(datas, d => d.time);
        // if (timeRange[0]!==undefined && timeRange[1]!==undefined){
        //     const timeDim = getDim(timeRange, -1, 'time');
        //     timeDim.tickFormat = (d: number) => new Date(d).toLocaleString();
        //     dimensions.push(timeDim);
        // }
        dimensions.sort((a, b) => a.order - b.order).forEach((d,i)=>d.order = i);
        const timeStamp = Object.values<Date>(timeObject).sort((a,b)=>+a - (+b))??[];
        dimensions.forEach(s=>{
            ranking.byMetric[s.key]=[]
        });
        const hosts = Object.keys(dataByTime);
        timeStamp.forEach((t,ti)=>{
            dimensions.forEach((ser:Dimension)=>{
                ranking.byMetric[ser.key][ti] = {};
                let sorteddata = hosts.map(h=>{
                    if(!ranking.byComputer[h])
                        ranking.byComputer[h] = {};
                    if(!ranking.byComputer[h][ser.key])
                        ranking.byComputer[h][ser.key] = [];
                    return {key:h,value:dataByTime[h][''+t][ser.key] as number};
                }).sort((a,b)=>-b.value+a.value);
                sorteddata.forEach((d,i)=>{
                    ranking.byMetric[ser.key][ti][d.key] = i;
                    ranking.byComputer[d.key][ser.key][ti] = i;
                    // d.user.forEach(u=>{
                    //     if (!ranking.byUser[u][ser.text][d.key])
                    //         ranking.byUser[u][ser.text][d.key] = [];
                    //     ranking.byUser[u][ser.text][d.key][ti]=i;
                    // });
                });
            });
        })

        return ({dimensions,dataByTime,timeStamp,datas,ranking})
    }
    extractData(dataByTime:DataByTime,time:Date){
        const data: DataObject[] =[];
        Object.values(dataByTime).forEach(d=>{
            if (d[''+time])
                data.push(d[''+time])
        });
        return data;
    }
    render(){
        const styles = getStyles();
        const {width, height} = this.props;
        // const {nodeSize,nodePerRing,orderType} = this.props.options;
        const {nodeSize,numberOfRing,orderType} = this.props.options;
        const {data,dimensions,selectedService,selectedIndex,timeStamp} = this.state;
        // const numNodes = Object.keys(dataByTime).length;
        const r = this.r();
        const start = 0;
        // const end = Math.PI*2*(numNodes/nodePerRing);
        const end = Math.PI*2*(numberOfRing);
        const theta = function(r:number) {
            return -(r-start)-start;
        };
        const radius = d3.scaleLinear()
            .domain([start, end])
            .range([r*0.1, r]);
        const points = d3.range(start, end + 0.001, (end - start) / 1000);
        const spiral = d3.radialLine<number>()
            .curve(d3.curveCardinal)
            .angle(theta)
            .radius(radius);
        const selectedDim = dimensions.find(d=>d.key===selectedService);
        const range = selectedDim?[selectedDim.range[0]??0,selectedDim.range[1]??0]:[0,0];
        const color = d3.scaleSequential<string|number|null|Date|undefined>()
            .interpolator(d3.interpolateSpectral)
            .domain(range.slice().reverse());
        const spiralScale = d3.scaleLinear<number>()
            .domain(orderType==='rank'?[0,data.length-1]:range)
            .range([start, end]);
        const order = orderType ==='rank'?'order':selectedService;

        return <div style={{width:width+' px', height:height+' px', position:'relative'}}>
            <svg  className={styles.svg}
                     width={width}
                     height={height}
                     xmlns="http://www.w3.org/2000/svg"
                     xmlnsXlink="http://www.w3.org/1999/xlink"
                     viewBox={`0 0 ${width} ${height}`}
                     ref={this.svgRef}>

            <g className={"content"} transform={`translate(${this.centerX()},${this.centerY()})`}>
                <defs></defs>
                <path id="#spiral" d={''+spiral(points)} style={{fill:'none',stroke: 'lightgrey',strokeDasharray:"6 5",opacity:0.2}}/>
                <g>
                    {this.state.selectedData?this.draw_trajectory_contours(this.state.selectedData,orderType ==='rank'?this.state.ranking.byComputer[this.state.selectedData.id as string][selectedService]:Object.values(this.state.dataByTime[this.state.selectedData.id as string]).map(e=>e[selectedService] as number),radius,spiralScale,theta):''}
                </g>
                {data.map(d=>{
                    const anglePer = spiralScale(d[order??''] as number);
                    const a = theta(anglePer); // % distance are on the spiral
                    const lineR = radius(anglePer);
                    const x = lineR*Math.sin(a); // x postion on the spiral
                    const y = -lineR*Math.cos(a); // y position on the spiral
                    return <circle cx={x} cy={y} r={nodeSize} fill={''+color(d[selectedService??''] as number)}
                                   opacity={this.state.selectedData?(this.state.selectedData===d?1:0.1):1}
                                   onMouseOver={()=>{if(!this.state.freeze) this.setState({selectedData:d})}}
                                   onClick={()=>{if(this.state.freeze) this.setState({selectedData:undefined}); this.setState({freeze:!this.state.freeze})}}
                                   onMouseOut={()=>{if(!this.state.freeze)this.setState({selectedData:undefined})}}
                    />
                })}
            </g>
        </svg>
            <div style={{position:'absolute'}}>
            <select value={selectedService}
                onChange={(event)=>{this.sortByService(this.state.data,''+event.target.value);this.setState({selectedService:event.target.value})}}>
                {dimensions.map(d=><option key={d.key} value={d.key}>{d.key}</option>)}
            </select>
            <TimeSelection value={selectedIndex} timeStamp={timeStamp} onChange={this.onChangeTime.bind(this)}/>
            </div>
            {this.state.selectedData?<div className={styles.tooltip}>
                <table>
                    <thead><tr><th>Name</th><th>Value</th></tr></thead>
                    <tbody>
                        <tr><td className={styles.tableCell}>Node</td><td className={styles.tableCell}>{this.state.selectedData.id}</td></tr>
                        {dimensions.map(d=><tr style={{fontWeight: d.key===selectedService?'bold':'normal'}}><td className={styles.tableCell}>{d.key}</td><td className={styles.tableCell}>{this.state.selectedData?d3.format('.2f')(this.state.selectedData[d.key] as number):'-'}</td></tr>)}
                    </tbody>
                </table>
            </div>:''}
        </div>
    }
}

const getStyles = stylesFactory(() => {
    return {
        wrapper: css`
      position: relative;
    `,
        legend: css`
      position: absolute;
      top: 0;
      left: 0;
      overflow-y:auto;
      max-height: 50%;
    `,
        legendColor: css`
      width: 10px;
      height: 10px;
      margin-right:2px;
      display: inline-block;
    `,
        svg: css`
      position: absolute;
      top: 0;
      left: 0;
    `,
        tooltip: css`
            position: absolute;
      top: 0;
      right: 0;
        `,
        textBox: css`
      position: absolute;
      bottom: 0;
      left: 0;
      padding: 10px;
    `,
        tableCell:css`
         padding-left: 5px;
         padding-right: 5px;
         `
    };
});
