import { PanelPlugin } from '@grafana/data';
import { SimpleOptions } from './types';
import { SimplePanel } from './SimplePanel';

export const plugin = new PanelPlugin<SimpleOptions>(SimplePanel).setPanelOptions(builder => {
    return builder
        .addSelect({
            path: 'orderType',
            name: 'Order Type',
            defaultValue : 'rank',
            settings:{
                options: [{label:'Ranking',value:'rank'},{label:'Metric',value:'value'}]
            }
        })
        // .addNumberInput({
        //     path: 'nodePerRing',
        //     name: '#Node per ring',
        //     defaultValue: 50
        // })
        .addNumberInput({
            path: 'numberOfRing',
            name: '#Ring',
            defaultValue: 5
        })
        .addNumberInput({
            path: 'nodeSize',
            name:'Node Size',
            defaultValue: 5
        })
    // .addSliderInput({
        //     path: 'lineOpacity',
        //     name:'Line Opacity',
        //     defaultValue: 0.5,
        //     settings: {min:0,
        //         max:1,
        //         step: 0.01}
        // })
        // .addSelect({
        //     path: 'axisDisplay',
        //     name: 'Axis display',
        //     defaultValue: 'left',
        //     settings:{
        //         options: [{value: 'none',label:'Hidden'},
        //             {value: 'left',label:'Left'},
        //             {value: 'right',label:'Right'}]
        //     }
        // })
});
